/*
* === Page ===
*/

.viget {

}

.viget h1 {
    color: white;
    font-size: calc(18px + 4vw);
    text-align: center;
}

.viget h2 {
    color: #1496BB;
    display: inline-block;
    font-size: 24px;
    letter-spacing: 1px;
    padding-left: 1em;
}

.viget p {
    font-size: 18px;
}

.viget section {
    height: auto;
    margin-bottom: 54px;
}

.viget article {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    padding: 0 15px;

    border: 1px solid black;
}

.viget article .heading {
    -webkit-box-flex: 1;
        -ms-flex: 1 1;
            flex: 1 1;
    padding-right: 1em;
}

.viget article .content {
    border-left: 2px solid #F26D21;
    -webkit-box-flex: 1.5;
        -ms-flex: 1.5 1;
            flex: 1.5 1;
    margin: 0 auto;
    padding-bottom: 44px;
    padding-left: 1em;
    width: 100%;
}

.viget article img {
    height: auto;
    max-width: 300px;
    width: 100%;
}

.viget .intro {
    background-color: #FF00FF;
    background-image: linear-gradient(19deg, #FF00FF 0%, #1496BB 100%);
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;

    height: 75vh
}

.viget .intro span {
    font-size: 50px;
    text-align: center;
}

.viget-logo {
    margin-bottom: 1em;
}

.viget-logo-svg {
    height: auto;
    width: 100px;
}



